import Audio from './Audio.svelte';
import Video from './Video.svelte';
export default function media({ videoAttrs = { controls: true }, audioAttrs = { controls: true } } = {}) {
    return {
        render(node) {
            if (node.type !== 'element')
                return;
            switch (node.tagName) {
                case 'video': {
                    const { src, poster, width, height } = node.properties;
                    return {
                        component: Video,
                        props: { src, poster, width, height, attrs: videoAttrs }
                    };
                }
                case 'audio': {
                    return {
                        component: Audio,
                        props: {
                            src: node.properties.src,
                            attrs: audioAttrs
                        }
                    };
                }
            }
        }
    };
}
